<template>
    <div>
        <span class="p-2 mx-1 bg-light">
          <img :src="guest.profile.photo" width="20">
          {{ guest.profile.name }} , 
          <span class="text-white" :class="{'bg-danger':guest.status == 3, 'bg-success':guest.status == 2, 'bg-secondary': guest.status == 1}">{{ status[guest.status] }}</span>
        </span>
    </div>
</template>

<script>
export default {
  data() {
    return {
      status: {
        1: 'Pending',
        2: 'Accepted',
        3: 'Rejected'
      }
    }
  },
  props: {
    guest: {
      type: Object
    }
  }
}
</script>