<template>
  <div>
    <div v-if="loading">Loading...</div>
    <div v-if="event">
      <img :src="event.data.owner.profile.photo" width="150">
      <div v-if="event.can_view_guest">
        <div class="my-3">
          <router-link :to="{name: 'viewGuests', params: {id: parseInt(event.data.id)}}">View guests</router-link> |
          <router-link :to="{name: 'addGuest'}">Add guest</router-link> | 
          <router-link :to="{name: 'viewGallery', params: {id: parseInt(event.data.id)}}">Gallery</router-link>
        </div>
      </div>
      <div>
        <router-view></router-view>
      </div>
      <div class="my-3">
        <single-guest v-for="guest in event.guests" :key="guest.id" :guest="guest"></single-guest>
      </div>
      <h1>{{ event.data.title }}</h1>
      <h4 class="text-danger" v-show="event.data.expired">Expired</h4>
      <div>{{ event.data.due_date_string }}</div>
      <div>{{ event.data.description }}</div>
      <br>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

// import EventService from '@/services/event.service';
import SingleGuest from '@/components/events/guests/SingleGuest'

export default {
  components: {
    SingleGuest
  },
  data() {
    return {
      loading: true,
      canViewGuests: false,
      guests: [],
    }
  },
  props: {
    id: {
      type: String
    }
  },
  created() {
    this.loading = true;
    this.$store.dispatch('event/getById', parseInt(this.id))
      .then(()=> {
        this.loading = false;
      })
  },
  computed: {
    ...mapGetters('event', [
      'getViewed'
    ]),
    event() {
      return this.getViewed(this.id);
    }
  },
  mounted() {
    //setTimeout(()=> console.log(this.getViewed(4)), 2000)
  },
}
</script>